import yum from '../../../components/yum'
export const ARTICLE = 'ARTICLE';
export const CATLIST = 'CATLIST';
export const CID = 'CID';
export const ITEM = 'ITEM';
export const state =() => {
    return {
        catlist : [],
        artlist : [],
        selectCid: null,
        selectItem:null
    }
};
export const mutations = {
    [CATLIST](state,cat){
        state.catlist = cat
    },
    [ARTICLE](state,art){
        state.artlist = art
    },
    [CID](state,cid){
        state.selectCid = cid
    },
    [ITEM](state,name){
        state.selectItem = name
    }
};
export const actions = {
    getCatlist({commit}){
        yum('api/article/getCategory').then(res=>{
            if (res.data.status === 200){
                let catlist = res.data.data.catList;
                let cache = [];
                catlist.forEach(item=>{
                    cache.push(Object.assign(item,{show:false}))
                })
                commit(CATLIST,cache);
                commit(ARTICLE,res.data.data.arList);
            }
        })
    },
    chooseACid({commit},cid){
        commit(CID,cid)
    },
    chooesedName({commit},name){
        commit(ITEM,name)
    }
}