import Vue from 'vue'
import Router from 'vue-router'
import promise from 'es6-promise'
promise.polyfill();
Vue.use(Router);
export default new Router({
    mode: 'hash',
    scrollBehavior(to, from, savedPosition) {
        return {x: 0, y: 0}
    },
    routes:[
        {
            path:'/',
            name:'index',
            component:()=>import('./view/index')
        },{
            path:'/:aid',
            name:'paragaph',
            component:()=>import('./view/page')
        }
    ]
})